<template>
  <div id="app">
    <div v-show="!ready" class="tips">
      <el-empty description="需要刷新与录入数据，请切换到广告组页面，并选择单日的时间"></el-empty>
    </div>
    <el-table v-show="ready"
              size="small"
              :data="tableData"
              style="width: 100%">
      <el-table-column prop="impressions" label="展示次数" width="75">
        <template v-slot="scope">
          <div> {{ scope.row.impressions + parseInt(scope.row.inflated.impressions) }}</div>
        </template>
      </el-table-column>
      <el-table-column label="增加" width="75">
        <template v-slot="scope">
          <el-input v-model="scope.row.inflated.impressions" oninput="value=value.replace(/[^\d]/g,'')"
                    size="mini"></el-input>
        </template>
      </el-table-column>
      <el-table-column prop="clicks" label="点击次数" width="75">
        <template slot-scope="scope">
          <div> {{ scope.row.clicks + parseInt(scope.row.inflated.clicks) }}</div>
        </template>
      </el-table-column>
      <el-table-column label="增加" width="75">
        <template v-slot="scope">
          <el-input v-model="scope.row.inflated.clicks" oninput="value=value.replace(/[^\d]/g,'')"
                    size="mini"></el-input>
        </template>
      </el-table-column>
      <el-table-column label="花费金额" width="80">
        <template v-slot="scope">
          <span>{{ (parseFloat(scope.row.cost) + parseFloat(scope.row.inflated.cost)).toFixed(2) }}</span>
        </template>
      </el-table-column>
      <el-table-column label="增值" width="80">
        <template v-slot="scope">
          <el-input v-model="scope.row.inflated.cost" @input="handleEdit(scope.row)" size="mini"></el-input>
        </template>
      </el-table-column>
      <el-table-column label="成效" width="80">
        <template slot-scope="scope">
          <div> {{ scope.row.install_conversions + parseInt(scope.row.inflated.install_conversions) }}</div>
        </template>
      </el-table-column>
      <el-table-column label="增加" width="80">
        <template v-slot="scope">
          <el-input v-model="scope.row.inflated.install_conversions" oninput="value=value.replace(/[^\d]/g,'')"
                    size="mini"></el-input>
        </template>
      </el-table-column>
      <el-table-column label="单次成效" width="70">
        <template v-slot="scope">
            <span>{{
                ((scope.row.cost + parseFloat(scope.row.inflated.cost)) / (scope.row.install_conversions + parseInt(scope.row.inflated.install_conversions ?? 0))).toFixed(2)
              }}</span>
        </template>
      </el-table-column>
      <el-table-column label="其他事件" type="expand" width="70">
        <template v-slot="scope">
          <el-row :gutter="20">
            <el-col :span="20">
              <label>互动次数</label>
              <span>{{ scope.row.interactions + parseInt(scope.row.inflated.interactions) }}</span>
              <el-input v-model="scope.row.inflated.interactions" oninput="value=value.replace(/[^\d]/g,'')"
                        size="mini"></el-input>
            </el-col>
            <el-col :span="20">
              <label>转换次数</label>
              <span>{{ scope.row.conversions + parseInt(scope.row.inflated.conversions) }}</span>
              <el-input v-model="scope.row.inflated.conversions" oninput="value=value.replace(/[^\d]/g,'')"
                        size="mini"></el-input>
            </el-col>
            <el-col :span="20">
              <label>安装次数</label>
              <span>{{ scope.row.install + parseInt(scope.row.inflated.install) }}</span>
              <el-input v-model="scope.row.inflated.install" oninput="value=value.replace(/[^\d]/g,'')"
                        size="mini"></el-input>
            </el-col>
          </el-row>
        </template>
      </el-table-column>
    </el-table>
  </div>

</template>

<script>

export default {
  name: 'app',
  created: function () {
    this.init()
  },
  props: {
    ready: false,
    data: []
  },
  data() {
    return {
      tableData: this.data
    }
  },
  watch: {
    data: {
      handler(newVal, oldVal) {
        this.tableData = newVal
      },
      immediate: true,
      deep: true
    },
    tableData: {
      handler(newVal, oldVal) {
        let total = 0;
        newVal.forEach(function (item) {
          if (parseFloat(item.inflated.cost) > 0) {
            total += parseFloat(item.inflated.cost);
          }
        })
        this.$emit("totalUpdated", total)
      },
      immediate: true,
      deep: true
    }
  },
  methods: {
    async init() {

      const that = this;


    },
    handleEdit(scope) {
      let value = scope.inflated.cost.replace(/[^\-\d.]/g, '') // 只能输入.和-和数字
      value = value.replace(/^\./g, '')  //第一个字符不能是.
      value = value.replace(/\.{2,}/g, '.') // 不能连续输入.
      value = value.replace(/(\.\d+)\./g, '$1') // .后面不能再输入.
      value = value.replace(/(-)\./g, '$1') // -后面不能输入.
      value = value.replace(/\-{2,}/g, '-') // -只能保留一个
      value = value.replace(/(\d+|\.)-/g, '$1') // 数字和.后面不能接-,不能出现类似11-, 12.-
      value = value.replace(/-(0){2,}/g, "$1") // 不能出现-00,-001,-0001类似
      value = value.replace(/(-)0+(\d+)/g, '$1$2') // 不能出现-01,-02类似
      value = value.replace(/^0+(\d)/, '$1') // 第一位0开头，0后面为数字，则过滤掉，取后面的数字
      value = value.replace(/(\d{15})\d*/, '$1') // 最多保留15位整数
      value = value.replace(/(\.\d{2})\d*/, '$1')// 最多保留2位小数
      scope.inflated.cost = value === '' ? 0 : value
    },
    save(data) {
      let that = this;

      let list = [];
      this.tableData.forEach(function (item) {
        list.push({
          account: item.account,
          campaign: item.campaign,
          group: item.group,
          impressions: item.impressions,
          interactions: item.interactions,
          conversions: item.conversions,
          clicks: item.clicks,
          install: item.install,
          install_conversions: item.install_conversions,
          cost: item.cost,
          day: data.day,
          cost_inflated: item.inflated.cost,
          inflated: {
            impressions: item.inflated.impressions === '' ? 0 : parseInt(item.inflated.impressions),
            interactions: item.inflated.interactions === '' ? 0 : parseInt(item.inflated.interactions),
            clicks: item.inflated.clicks === '' ? 0 : parseInt(item.inflated.clicks),
            conversions: item.inflated.conversions === '' ? 0 : parseInt(item.inflated.conversions),
            install: item.inflated.install === '' ? 0 : parseInt(item.inflated.install),
            install_conversions: item.inflated.install_conversions === '' ? 0 : parseInt(item.inflated.install_conversions),
            cost: item.inflated.cost === '' ? 0 : parseFloat(item.inflated.cost)
          }
        })
      })
      data.list = list;
      chrome.tabs && chrome.tabs.query({active: true, currentWindow: true}, function (tabs) {
        chrome.tabs.sendMessage(tabs[0].id, {name: "MetaAI", action: "save", data: data}, function (response) {
          that.$message.success({message: '保存成功', duration: 1500});
          console.log(response)
        });
      });
      console.log(data)
    }
  }
}
</script>

<style scoped>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin: 0;
  width: 100%;
  background-color: #f3f3f3;
  padding: 0;
  min-height: 260px;
  position: relative;
}

.tips {
  text-align: center;
  color: #2c3e50;
  margin: 10px 0 0;
  background-color: #ffffff;
  padding: 10px;
  min-height: 260px;
  position: relative;
}


.el-table {
  margin: 10px 0 0;
}

.el-table--small .el-table__cell {
  padding: 6px 0;
}

.el-row {
  background-color: #f7f7f7;
  margin-top: -8px;
  padding: 6px 0;
}

.el-row > div {
  display: flex;
  align-items: center;
  padding: 6px 10px;
}

.el-row label {
  width: 100px;
  text-align: right;
}

.el-row span {
  width: 100px;
  text-align: center;
  font-weight: bold;
}

.el-row .el-input {
  width: 80px;
}

</style>
