<template>
  <div id="app">
    <div v-show="!ready" class="tips">
      <el-empty description="需要刷新与录入数据，请切换到广告页面，并选择单日的时间"></el-empty>
    </div>
    <el-table v-show="ready"
              size="small"
              :data="tableData"
              style="width: 100%">
      <el-table-column prop=reach label="覆盖人数" width="70">
        <template v-slot="scope">
          <div> {{ parseInt(scope.row.reach) + parseInt(scope.row.inflated.reach) }}</div>
        </template>
      </el-table-column>
      <el-table-column label="增加" width="80">
        <template v-slot="scope">
          <el-input v-model="scope.row.inflated.reach" oninput="value=value.replace(/[^\d]/g,'')" size="mini"></el-input>
        </template>
      </el-table-column>
      <el-table-column prop=impressions label="展示次数" width="70">
        <template v-slot="scope">
          <div> {{ parseInt(scope.row.impressions) + parseInt(scope.row.inflated.impressions) }}</div>
        </template>
      </el-table-column>
      <el-table-column label="增加" width="80">
        <template v-slot="scope">
          <el-input v-model="scope.row.inflated.impressions" oninput="value=value.replace(/[^\d]/g,'')" size="mini"></el-input>
        </template>
      </el-table-column>
      <el-table-column label="花费金额" width="80">
        <template v-slot="scope">
          <span>{{ (parseFloat(scope.row.spend) + parseFloat(scope.row.inflated.spend)).toFixed(2) }}</span>
        </template>
      </el-table-column>
      <el-table-column label="增值" width="80">
        <template v-slot="scope">
          <el-input v-model="scope.row.inflated.spend" @input="handleSpendEdit(scope.row)" size="mini"></el-input>
        </template>
      </el-table-column>
      <el-table-column label="成效" width="60">
        <template v-slot="scope">
          <span>{{ (scope.row.results + parseInt(scope.row.inflated.results ?? 0)) }}</span>
        </template>
      </el-table-column>
      <el-table-column label="加成效" width="80">
        <template v-slot="scope">
          <el-input v-model="scope.row.inflated.results" oninput="value=value.replace(/[^\d]/g,'')"
                    size="mini"></el-input>
        </template>
      </el-table-column>
      <el-table-column
          prop="cost_per_result" label="单次成效" width="80">
        <template v-slot="scope">
            <span>{{
                ((scope.row.spend + parseFloat(scope.row.inflated.spend)) / (scope.row.results + parseInt(scope.row.inflated.results ?? 0))).toFixed(2)
              }}</span>
        </template>
      </el-table-column>
      <el-table-column label="其他事件" type="expand" width="80">
        <template v-slot="scope">
          <el-row :gutter="20">
            <el-col :span="20">
              <label>点击</label>
              <span>{{ parseInt(scope.row.clicks) + parseInt(scope.row.inflated.clicks ?? 0) }}</span>
              <el-input v-model="scope.row.inflated.clicks" oninput="value=value.replace(/[^\d]/g,'')"
                        size="mini"></el-input>
            </el-col>
            <el-col :span="20" v-for="(item, index) in scope.row.action" :key="item.name">
              <label>{{ item.name | meta-event-trans }}</label>
              <span>{{ parseInt(item.value) + parseInt(scope.row.inflated.action[index].value) }}</span>
              <el-input v-model="scope.row.inflated.action[index].value" oninput="value=value.replace(/[^\d]/g,'')"
                        size="mini"></el-input>

            </el-col>
          </el-row>
        </template>
      </el-table-column>
    </el-table>
  </div>

</template>

<script>

//https://docs.dataddo.com/docs/facebook-ads-fields-references
import MetaEvent from "@/assets/meta-event.json";

export default {
  name: 'app',
  created: function () {
    this.init()
  },
  props: {
    ready: false,
    data: []
  },
  data() {
    return {
      tableData: this.data,
      MetaEvent: MetaEvent
    }
  },
  watch: {
    data: {
      handler(newVal, oldVal) {

        this.tableData = JSON.parse(JSON.stringify(newVal))

        this.tableData.forEach(function (item) {
          item.action = item.action.filter(function (sub) {
            return MetaEvent.find(o => o.event === sub.name);
          })

          item.inflated.action = item.inflated.action.filter(function (sub) {
            return MetaEvent.find(o => o.event === sub.name);
          })
        })

      },
      immediate: true,
      deep: true
    },
    tableData: {
      handler(newVal, oldVal) {
        let total = 0;
        newVal.forEach(function (item) {
          if (parseFloat(item.inflated.spend) > 0) {
            total += parseFloat(item.inflated.spend);
          }
        })
        this.$emit("totalUpdated", total)
      },
      immediate: true,
      deep: true
    }
  },
  filters: {
    "meta-event-trans"(value) {
      let find = MetaEvent.find(o => o.event === value);
      return find ? find.name : value;
    },
  },
  methods: {
    async init() {

      const that = this;


    },
    handleSpendEdit(scope) {
      let value = scope.inflated.spend.replace(/[^\-\d.]/g, '') // 只能输入.和-和数字
      value = value.replace(/^\./g, '')  //第一个字符不能是.
      value = value.replace(/\.{2,}/g, '.') // 不能连续输入.
      value = value.replace(/(\.\d+)\./g, '$1') // .后面不能再输入.
      value = value.replace(/(-)\./g, '$1') // -后面不能输入.
      value = value.replace(/\-{2,}/g, '-') // -只能保留一个
      value = value.replace(/(\d+|\.)-/g, '$1') // 数字和.后面不能接-,不能出现类似11-, 12.-
      value = value.replace(/-(0){2,}/g, "$1") // 不能出现-00,-001,-0001类似
      value = value.replace(/(-)0+(\d+)/g, '$1$2') // 不能出现-01,-02类似
      value = value.replace(/^0+(\d)/, '$1') // 第一位0开头，0后面为数字，则过滤掉，取后面的数字
      value = value.replace(/(\d{15})\d*/, '$1') // 最多保留15位整数
      value = value.replace(/(\.\d{2})\d*/, '$1')// 最多保留2位小数
      scope.inflated.spend = value === '' ? 0 : value
    },
    save(data) {
      let that = this;

      let list = [];
      that.tableData.forEach(function (item) {


        if (!item.inflated.results || item.inflated.results === "") {
          item.inflated.results = 0;
        }
        //item.inflated.action = [];
        // item.action.forEach(function (sub) {
        //   item.inflated.action.push({
        //     name: sub.name,
        //     value: Math.floor(sub.value * item.inflated.results / item.results),
        //   })
        // })

        list.push({
          ad_id: item.ad_id,
          adset_id: item.adset_id,
          campaign_id: item.campaign_id,
          reach: item.reach,
          impressions: item.impressions,
          spend: item.spend,
          day: data.day,
          clicks: item.clicks,
          results: item.results,
          action: item.action,
          inflated: {
            reach: item.inflated.reach === '' ? 0 : parseInt(item.inflated.reach),
            impressions: item.inflated.impressions === '' ? 0 : parseInt(item.inflated.impressions),
            spend: item.inflated.spend === '' ? 0 : parseFloat(item.inflated.spend),
            clicks: item.inflated.clicks === '' ? 0 : parseFloat(item.inflated.clicks),
            results: item.inflated.results === '' ? 0 : parseFloat(item.inflated.results),
            action: item.inflated.action.filter(function (sub) {
              sub.value = sub.value === '' ? 0 : parseInt(sub.value);
              return true;
            })
          }
        })
      })
      data.list = list;
      chrome.tabs && chrome.tabs.query({active: true, currentWindow: true}, function (tabs) {
        chrome.tabs.sendMessage(tabs[0].id, {name: "MetaAI", action: "save", data: data}, function (response) {
          that.$message.success({message: '保存成功', duration: 1500});
          console.log(response)
        });
      });
      console.log(data)
    }
  }
}
</script>

<style scoped>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin: 0;
  width: 100%;
  background-color: #f3f3f3;
  padding: 0;
  min-height: 260px;
  position: relative;
}

.tips {
  text-align: center;
  color: #2c3e50;
  margin: 10px 0 0;
  background-color: #ffffff;
  padding: 10px;
  min-height: 260px;
  position: relative;
}


.el-table {
  margin: 10px 0 0;
}

.el-table--small .el-table__cell {
  padding: 6px 0;
}

.el-input--mini .el-input__inner{
  padding: 0 6px;
}

.el-row {
  background-color: #f7f7f7;
  margin-top: -8px;
  padding: 6px 0;
}

.el-row > div {
  display: flex;
  align-items: center;
  padding: 6px 10px;
}

.el-row label {
  width: 100px;
  text-align: right;
}

.el-row span {
  width: 100px;
  text-align: center;
  font-weight: bold;
}

.el-row .el-input {
  width: 80px;
}

</style>
