<template>
  <div id="app">
    <div v-show="actived">
      <div class="header">
        <div>
          <el-date-picker v-model=day type="date" size="small" readonly="readonly" width="60" placeholder="选择日期">
          </el-date-picker>
          <el-button class="fresh" type="primary" size="small" :disabled="!ready" v-on:click="fresh" plain>获取数据
          </el-button>
          <el-button class="save" type="primary" size="small" :disabled="!ready" v-on:click="save">保存</el-button>
          <span class="inflate">{{ total }}</span>
        </div>
        <el-switch style="display: block" v-model="protect" active-color="#13ce66" inactive-color="#e3e3e3"
                   active-text="" inactive-text="数据防护" @change="protectChanged">
        </el-switch>
      </div>
      <meta-panel v-if="platform==='meta'" ref="panel" :data="tableData" :ready="ready"
                  @totalUpdated="totalUpdated"></meta-panel>
      <google-panel v-if="platform==='google'" ref="panel" :data="tableData" :ready="ready"
                    @totalUpdated="totalUpdated"></google-panel>
    </div>
    <div v-show="!actived">
      <el-empty description="请联系管理员激活">
        {{ visitorId }}
      </el-empty>
    </div>
  </div>
</template>

<script>
import MetaPanel from "@/components/MetaPanel.vue";
import GooglePanel from "@/components/GooglePanel.vue";

export default {
  name: 'app',
  components: {
    MetaPanel,
    GooglePanel
  },
  created: function () {
    this.init()
  },
  data() {
    return {
      actived: false,
      visitorId: '',
      ready: false,
      day: '',
      account: 0,
      protect: false,
      platform: 'meta',
      total: 0,
      tableData: []
    }
  },
  methods: {
    async init() {

      const that = this;

      chrome.tabs && chrome.tabs.query({active: true, currentWindow: true}, function (tabs) {
        chrome.tabs.sendMessage(tabs[0].id, {name: "MetaAI", action: "init"}, function (response) {

          console.log('MetaAI.init');
          console.log(response);

          that.platform = response.platform;
          that.account = response.account;
          that.protect = response.protect;
          that.ready = response.ready;
          that.visitorId = response.visitorId;

          that.$http.get('/meta/active?id=' + that.visitorId).then(res => {
            that.actived = res.data.data;
            console.log(res)
          })

        });
      });

      chrome.runtime && chrome.runtime.onMessage.addListener(function (request, sender, sendResponse) {
        if (request.name === "MetaAI" && request.action === "sending") {

          sendResponse(JSON.stringify(request));

          if (request.data.since !== request.data.until) {
            that.$message.error({message: '请切换到广告Tab，并选择单日日期（不能选择范围）', duration: 1500});
          } else {
            that.day = request.data.since;
            that.account = request.data.account;
            that.tableData = request.data.list;
          }

        }
      });

    },
    totalUpdated(total) {
      this.total = total;
    },
    protectChanged(event) {
      var that = this
      chrome.tabs && chrome.tabs.query({active: true, currentWindow: true}, function (tabs) {
        chrome.tabs.sendMessage(tabs[0].id, {
          name: "MetaAI",
          action: "protect",
          data: {
            account: that.account,
            protect: event
          }
        }, function (response) {
          // 回调函数(传回的信息)
          console.log(response)
        });
      });
    },
    fresh() {
      chrome.tabs && chrome.tabs.query({active: true, currentWindow: true}, function (tabs) {
        chrome.tabs.sendMessage(tabs[0].id, {name: "MetaAI", action: "fetching"}, function (response) {
          console.log(response)
        });
      });
    },
    save() {
      this.$refs.panel.save({
        account: this.account,
        protect: this.protect,
        day: this.day
      })

    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin: 0;
  width: 760px;
  background-color: #f3f3f3;
  padding: 10px;
  min-height: 260px;
  position: relative;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.el-date-editor.el-input {
  width: 120px;
}

.el-input--suffix .el-input__inner {
  padding-right: 0;
}

.el-input--mini .el-input__inner {
  height: 25px;
  line-height: 25px;
  padding: 0 6px;
}

.fresh {
  margin-left: 15px;
}

.inflate {
  display: inline-block;
  margin-left: 35px;
  color: #409EFF;
  font-weight: bold;
  background-color: #ffffff;
  padding: 6px 15px;
}

.save {
  margin-left: 20px;
}

</style>
